.Button {
    padding: 10px 25px;
    outline: none;
    cursor: pointer;
    background-color: white;
    font-weight: 700;
    letter-spacing: 1px;

}

.Button:hover:enabled {
    background-color: black;
    color: white;
    border-color: black;
}

.Button:disabled {
    cursor: not-allowed;
}

@media (min-width: 480px) {
    .Button {
        padding: 8px 9px;
    }
}
@media (min-width: 900px) {
    .Button {
        padding: 10px 25px;
    }
}